export function chain(source, fallback) {
    return (type, c) => source(type, c) ?? fallback(type, c);
}
export function fromRecord(record) {
    return (type) => record[type];
}
export function fromFactories(factories) {
    return (type, config) => factories[type]?.(config);
}
export function overrideByRecord(resolver, override) {
    return chain(fromRecord(override), resolver);
}
export function extendByRecord(resolver, extension) {
    return chain(resolver, fromRecord(extension));
}
